import React, { FC, ReactElement, useEffect, useState } from "react";
import {
  FC_GetOutgoingReportExcel,
  GetTransferForUser__Interface,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";

type OutGoingExcelProps = {
  close: () => void;
  district_id: string;
  district_name: string | null;
};

const OutGoingExcel: FC<OutGoingExcelProps> = ({
  close,
  district_id,
  district_name,
}): ReactElement => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState<GetTransferForUser__Interface[]>([]);
  const [error, setError] = useState("");
  /* function body */
  useEffect(() => {
    if (loading === true)
      FC_GetOutgoingReportExcel(district_id, (status, data, msg) => {
        setLoading(false);
        setData(data);
        setError(msg);
        // GetTransferForUser__Interface
        console.log({ data });
      });
  }, [loading, district_id]);

  return (
    <Modal
      backDrop={true}
      theme={Themes.default}
      close={close}
      backDropClose={false}
      widthSizeClass={ModalSize.large}
      displayClose={true}
      padding={{
        title: true,
        body: false,
        footer: false,
      }}
      title="Export report"
    >
      <div className="border-t p-4 py-8">
        {error !== "" && <Alert theme="danger">{error}</Alert>}
        {loading === true ? (
          <div className="text-xl text-center">
            <Loading />

            <h2 className="text-2xl font-bold pt-2">Preparing report</h2>
            <h4 className="text-gray-500 text-sm">Please wait...</h4>
          </div>
        ) : (
          <div className="text-center">
            <h1 className="text-3xl">
              Your <b>Outgoing Transfer</b> report is ready
            </h1>
            <h1>You can download it now</h1>
            <div className="inline-block pt-5">
              <ExportToExcel
                fileData={data.map((el) => ({
                  teacher_id: el.teacher_id,
                  teacher_first_name: el.teacher_first_name,
                  teacher_last_name: el.teacher_last_name,
                  teacher_full_name: el.teacher_full_name,
                  teacher_phone_number: el.teacher_phone_number,
                  position_code: el.position_code,
                  position_name: el.position_name,
                  qualification: el.qualification_name,

                  school_from_code: el.school_from_info?.school_code,
                  school_form_name: el.school_from_info?.school_name,
                  district_from_name: el.school_from_info?.district_name,

                  approved_school_code: el.approved_school_info?.school_code,
                  approved_school_name: el.approved_school_info?.school_name,
                  approved_district_name:
                    el.approved_school_info?.district_name,

                  transfer_reason: el.transfer_reason,
                  transfer_type: el.transfer_type,
                  teacher_requested_transfer_date:
                    el.teacher_requested_transfer_date,
                }))}
                fileName={`OUTGOING TRANSFERS - ${district_id} ${
                  district_name ? district_name : ""
                }`}
                btnName="Download EXCEL"
              />
            </div>
          </div>
        )}
      </div>
    </Modal>
  );
};
export default OutGoingExcel;
